import { Component , ElementRef, ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams , LoadingController } from 'ionic-angular';
import { CreatepostPage } from '../createpost/createpost';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { WordpressService } from '../../services/wordpress.service';
import { ViewController , Platform  } from 'ionic-angular';
/**
 * Generated class for the GalleryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-gallery',
  templateUrl: 'gallery.html',

    providers: [FileTransfer, FileTransferObject, File]
})
export class GalleryPage {

	morePagesAvailable: boolean = true;

	photos= [];
	post = {title: '',
			content: '',	
					 featuredimg: '',
					 imgid : '',
					 action: ''};
  			 
	file;
	fileoutput;
 filename;
  form: FormGroup;
  loading: boolean = false;


  @ViewChild('fileInput') fileInput: ElementRef;

  constructor(public navCtrl: NavController, public navParams: NavParams,
  	  public wordpressService: WordpressService,public viewCtrl: ViewController,
  	  public loadingCtrl: LoadingController,private transfer: FileTransfer, 
      private f: File ,public platform: Platform,
      private fb: FormBuilder) {

    this.createForm();


   }

  ionViewWillEnter() {

  	let loading = this.loadingCtrl.create({


  	});

  	if(this.photos.length == 0){

  	 
      loading.present();


  	}
   this.wordpressService.getImageWordpress().subscribe(data => { 
      
       this.photos = data;
       loading.dismiss();
        
       console.log(this.photos)     
    }, err => {
       console.log(err);
    });

   	  
     
      
  }











selecetimg(imgid , urlimg){

  if(this.wordpressService.imginstat == false){

this.wordpressService.imgidforgallery = imgid ;
this.wordpressService.urlimgforgallery = urlimg;
}

else{

this.wordpressService.imgins = urlimg;

}

 
this.navCtrl.pop();

  
}


 doInfinite(infiniteScroll) {

    let page = (Math.ceil(this.photos.length/10)) + 1;
    let loading = true;

    this.wordpressService.getImageWordpress(page)
    .subscribe(data => {
    	console.log(data);
      for(let photo of data){
        if(!loading){
          infiniteScroll.complete();
        }
        this.photos.push(photo);
        loading = false;
      }
    }, err => {
      this.morePagesAvailable = false;
    })
  }


createForm() {
    this.form = this.fb.group({
     
      avatar: null
    });
  }

  onFileChange(event) {
    if(event.target.files.length > 0) {
      let file = event.target.files[0];
      console.log(file)
      this.filename = file.name;
      document.getElementById('choose').innerHTML = this.filename;
      this.form.get('avatar').setValue(file);
    }
  }

  private prepareSave(): any {
    let input = new FormData();
   
    input.append('avatar', this.form.get('avatar').value);
    return input;
  }

  onSubmit() {
     let loading = this.loadingCtrl.create({

    content: 'Uploading...'

    });

    const formModel = this.prepareSave();
  
    console.log(formModel);

    
      // FormData cannot be inspected (see "Key difference"), hence no need to log it here
 
    loading.present({

    });


   
    // In a real-world app you'd have a http request / service call here like
    // this.http.post('apiUrl', formModel)

    
    const [file, extension] = this.filename.split('.');
    console.log(file)

    this.wordpressService.uploadimg(this.form.get('avatar').value , file) .subscribe(data => {
      
      console.log(data)
      loading.dismiss();

      this.ionViewWillEnter();

       
      });

  }

  clearFile() {
    this.form.get('avatar').setValue(null);
    this.fileInput.nativeElement.value = '';
  }


}



