  getRecentPosts(categoryId:number, page:number = 1){

  this.url =localStorage.getItem('url') + '/wp-json/wp/v2/'
    //if we want to query posts by category
    console.log(this.url);
    let token = localStorage.getItem('token');

   console.log(token)
 let header: Headers = new Headers( {'Content-Type': 'application/json',
   'Authorization': 'Bearer'+token})

    let category_url = categoryId? ("&categories=" + categoryId): "";

    

console.log(this.url  
      + 'posts?_embed&page=' + page
      + category_url)

    return this.http.get(
      this.url  
      + 'listing?_embed&page=' + page
      + category_url ,{ headers: header })


    .map(res => res.json()  );

    
    

  }

  getComments(postId:number, page:number = 1){
    console.log(this.url
      + "comments?listing=" + postId
      + '&page=' + page);
    return this.http.get(
      this.url
      + "comments?listing=" + postId
      + '&page=' + page)
    .map(res => res.json());
  }

  getAuthor(author){
    return this.http.get(this.url + "users/" + author)
    .map(res => res.json());
  }

  getPostCategories(post){
    let observableBatch = [];

    post.categories.forEach(category => {
      observableBatch.push(this.getCategory(category));
    });

    return Observable.forkJoin(observableBatch);
  }

  getCategory(category){
    return this.http.get(this.url + "categories/" + category)
    .map(res => res.json());
  }

  createComment(postId, user, comment){
    let header: Headers = new Headers();
    let token = localStorage.getItem('token');
    let display_name = localStorage.getItem('display_name');
    let email = localStorage.getItem('user_email');

    header.append('Authorization', 'Bearer ' + token);
    console.log(token)

    return this.http.post(this.url + "comments?token=" +  token, {
      author_name: display_name,
      author_email: email,
      post: postId,
      content: comment,
      status: 'approved'
    },{ headers: header })
    .map(res => res.json());
  }

