import { Injectable } from '@angular/core';
import { NativeStorage } from '@ionic-native/native-storage';
import { Http, Headers } from '@angular/http';
import { WordpressService } from './wordpress.service';
import * as Config from '../config';

@Injectable()
export class AuthenticationService {
url;

  constructor( public wordpressService: WordpressService,
    public nativeStorage: NativeStorage,
    public http: Http
  ){


  }

  getUser(){
    return localStorage.getItem('User');
  }

  setUser(user){
    return localStorage.setItem('User',user);
  }

  logOut(){
    sessionStorage.clear();
    return localStorage.clear();

  }

  doLogin(username, password , url){
    console.log(url)
    return this.http.post(url + '/wp-json/jwt-auth/v1/token',{
      username: username,
      password: password
    })
  }

  validateAuthToken(token){
    let header : Headers = new Headers();
    header.append('Authorization','Basic ' + token);
    return this.http.post(this.url + 'wp-json/jwt-auth/v1/token/validate?token=' + token,
      {}, {headers: header})
  }
}



