import { Component } from '@angular/core';
import { HomePage } from '../home/home'
import { NavController, LoadingController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { WordpressService } from '../../services/wordpress.service';
import { AuthenticationService } from '../../services/authentication.service';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {
  login_form: FormGroup;
  error_message: string;
  ishidden=true;
  src;
  loginok = false;


  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController,
    public formBuilder: FormBuilder,
    public wordpressService: WordpressService,
    public authenticationService: AuthenticationService
  ) {

 
      
  }

  ionViewWillLoad() {



    this.login_form = this.formBuilder.group({
      username: new FormControl('', Validators.compose([
        Validators.required
      ])),
      password: new FormControl('', Validators.required),
     

    });
  }


  ionViewDidLoad(){
      let loading = this.loadingCtrl.create();
    loading.present();

       document.getElementById("myFrame").src ="https://www.20by4.com/~sammys22/wp-login.php";


        document.getElementById("myFrame").onload = () => {
 
     loading.dismiss();
        

     
}
  

  }


  login(value){
    let loading = this.loadingCtrl.create();
    loading.present();



    this.authenticationService.doLogin(value.username, value.password , "https://www.20by4.com/~sammys22" )
    .subscribe(res => {
      console.log(res);

          console.log( res.json().token);

          console.log( res.json().user_display_name);
           console.log( res.json().user_email);

             localStorage.setItem('url', 'https://www.20by4.com/~sammys22')   
          localStorage.setItem('token', res.json().token)
          localStorage.setItem('display_name', res.json().user_display_name)
          localStorage.setItem('user_email', res.json().user_email)
          this.loginok=true  ;

       this.authenticationService.setUser(JSON.stringify(res));

    
     },
     err => {
       loading.dismiss();
       this.error_message = "Invalid Credentials. Check your URL, Username or Password";
       console.log(err);
       this.loginok=false;
     })

       document.getElementById("myFrame").onload = () => {


    

     


          this.ishidden = false;
        

         if(this.loginok == true){
          loading.dismiss();
         this.navCtrl.setRoot(HomePage);
       }

     }


          document.getElementById("myFrame").contentWindow.postMessage(value,   document.getElementById("myFrame").src)


  }


 

  skipLogin(){
    this.navCtrl.setRoot(HomePage);
  }

checkFocus(value){


}

   


}



