import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';


import { PostPage } from '../pages/post/post';
import { HomePage } from '../pages/home/home';
import { RegisterPage } from '../pages/register/register';
import { LoginPage } from '../pages/login/login';
import { GalleryPage } from '../pages/gallery/gallery';
import { CreatepostPage } from '../pages/createpost/createpost';
import { PopoverPage } from '../pages/popover/popover';


import { WordpressService } from '../services/wordpress.service';
import { AuthenticationService } from '../services/authentication.service';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativeStorage } from '@ionic-native/native-storage';
import { RichTextComponent } from '../components/rich-text/rich-text';
import "froala-editor/js/froala_editor.pkgd.min.js";
import { FroalaEditorModule, FroalaViewModule } from 'angular-froala-wysiwyg';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@NgModule({
  declarations: [
    MyApp,
    RichTextComponent,
    PostPage,
    HomePage,
    LoginPage,
    RegisterPage,
    CreatepostPage,
    PopoverPage,
    GalleryPage,
  
 

  ],
  imports: [
    FroalaEditorModule.forRoot(), FroalaViewModule.forRoot(), 
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/popover/popover.module#PopoverPageModule', name: 'PopoverPage', segment: 'popover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gallery/gallery.module#GalleryPageModule', name: 'GalleryPage', segment: 'gallery', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/createpost/createpost.module#CreatepostPageModule', name: 'CreatepostPage', segment: 'createpost', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    PostPage,
    HomePage,
    LoginPage,
    RegisterPage,
    CreatepostPage,
    PopoverPage,
    GalleryPage,
   
 
  ],
  providers: [
    StatusBar,
    SplashScreen,
    NativeStorage,
    WordpressService,
    AuthenticationService,
    InAppBrowser,
    {provide: ErrorHandler, useClass: IonicErrorHandler}

  ]
})
export class AppModule {}



